.scroll-up-container {
  display: flex;
  flex-flow: row;
  justify-content: center;
  padding: cre(60px) 0 cre(20px);
  width: 100%;
}

// scroll up button
.scroll-up {
  @include button-reset;
  font-size: 0;
  color: transparent;
  width: 30px;
  height: 30px;
  position: relative;

  &::before {
    @include font-icon('\e901');
    font-size: cre(20px);
    color: rgba(black, 0.3);
    position: absolute;
    top: 0;
    left: 50%;
    transform: rotate(-90deg) translate(0, -50%);
    width: 100%;
    height: 100%;
  }
}

// Button interaction
.cta-button {

  a {
    transform: scale(1);
    transition: 0.3s ease;

    &:focus,
    &:hover {
      transform: scale(1.1);
    }
  }
}